<script>
import TWEEN from '@tweenjs/tween.js';
import AppCss from '#/page/App.css';
import Aside from '#/page/App/component/Aside.vue';
export default {
    components:{
        Aside,
    },
    computed:{
        centerScrollTop(){
            return this.$store.state.HOME.scrollTop
        },
        scrollTop(){
            if(window.innerWidth<980){
                return window.scrollY
            }
            return this.$refs['center'].scrollTop
        }
    },
    watch:{
        centerScrollTop(newValue,oldValue){
            if(this.$store.state.HOME.scrollTopList.indexOf(newValue)===-1){
                return 
            }
            var VM=this;
            function anmt(){
                if(TWEEN.update()){
                    requestAnimationFrame(anmt)
                }
            }
            var coord={value:oldValue}
            new TWEEN.Tween(coord)
            .easing(TWEEN.Easing.Quadratic.Out)
            .to({ value: newValue }, 500)
            .onUpdate(function(){
                if(window.innerWidth<980){
                    return window.scrollTo(0,coord.value-VM.$store.state.navHeight);
                }
                VM.$refs['center'].scrollTop=coord.value||0;
            })
            .start();

            anmt();
        }
    },
    methods:{
        atv_mouseWheelCenter(){
            if(window.innerWidth>980&&this.$refs['center'].scrollTop!==0){
                this.$store.commit('SCROLL_HOME',this.$refs['center'].scrollTop)
            }
        }
    },
    mounted(){
        this.$refs['center'].scrollTop=this.centerScrollTop;
        if(window.innerWidth<980){
            window.addEventListener('scroll',e=>{
                this.$store.commit('SCROLL_HOME',window.scrollY);
            })
        }
    }
}
</script>
<template>
    <div id="main">
        <Aside></Aside>
        <div id="center" ref="center" @mousewheel="atv_mouseWheelCenter">
            <div id="router">
                <router-view></router-view>
            </div>
        </div>
    </div>
</template>



