import * as types from '../mutations-type.js';

const state = {
    scrollTopList: [0],
    scrollTop: 0
};
const getter = {};
const actions = {};
const mutations = {
    [types.SCROLL_HOME](state, scrollTop) {
        state.scrollTop = scrollTop;
    },
    [types.COMMIT_TOP_LIST](state, list) {
        state.scrollTopList = list;
    },
};

export default {
    state,
    getter,
    actions,
    mutations
}


