<template>
    <aside class="sd-hd" ref="sd-hd" :style="asideBg">
        <header :style="headerScale">
            <div class="hd-img">
                <img class="hd-img__img" :src="headImg"/>
            </div>
            <div class="hd-title">
                <h1>挺问原</h1>
            </div>
        </header>
        <section :class="navClass" ref="navSec">
            <nav class="sd-hd__nav" :style="asideBg" >
                <a 
                    v-for="(item,index) in navList"
                    v-text="item.text" 
                    :key="item.href" 
                    @click="atv_navClick($event,index)"
                    href=''>
                </a>
            </nav>
        </section>
        <footer></footer>
    </aside>
</template>
<script>
export default {
    data:_=>({
        headImg:require('#/public/img/hd.jpg'),
        navList:[{
            text:'首页',
            href:'#/'
        },{
            text:'文章列表',
            href:'#ARTICLE_LIST'
        },{
            text:'github项目',
            href:'#GITHUB_PROJECT'
        },{
            text:'友情链接',
            href:'#FRIEND_LINK'
        }],
        pageScrollTop:window.pageYOffset
    }),
    computed:{
        headerScale(){
            var n=(200-this.pageScrollTop)/200;
            n=n>1?1:n;
            n=n<=0?0:n;
            return {
                'transform':`scale(${n},${n})`,
                'transform-origin':`50% 100%`,
                'opacity':n ,
            }
        },
        navClass(){
            if(this.pageScrollTop>200){
                return 'z-top'
            }
            return ''
        },
        asideBg(){
            var n=(200-this.pageScrollTop)/200;
            n=n>1?1:n;
            n=n<=0?0:n;
            var c=(1-n)*60;
            return {
                'background':`rgba(${c},${c},${c},${0.5+(1-n)*0.5})`,
            }
        }
        
    },
    methods:{
        atv_navClick(e,i){
            e.preventDefault();
            var a=this.$store.state.HOME.scrollTopList;
            this.$store.commit('SCROLL_HOME',a[i]?a[i]:0);
        }
    },
    mounted(){
        window.addEventListener('scroll',e=>{
            this.pageScrollTop=window.pageYOffset
        });
        this.$nextTick(_=>{
            this.$store.commit('GET_NAV_HEIGHT',this.$refs['navSec'].offsetHeight);
            this.$refs['sd-hd'].style.paddingBottom=`${this.$refs['navSec'].offsetHeight}px`
        })
    }
}
</script>
<style scoped>
.sd-hd {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 200px;
}
.sd-hd header{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height:200px;
    width:100%;
    background:rgba(0,0,0,0.5);
    
}
.sd-hd section{
    width:100%;
    margin-top:15px;
}
.hd-img__img{
    width:100px;
    height:100px;
    padding:5px;
    border-radius:55px;
    background:#fff;
}
.hd-title h1{
    font-weight: 100;
    color:#fff;
}

.sd-hd__nav{
    display: block;
    width: 100%;
    padding:10px 0;
    background:rgba(0,0,0,0.5)
}
.sd-hd__nav a{
    display: block;
    width:100%;
    margin-bottom:15px;
    color:#fff;
    text-decoration: none;
    text-align:center;
}
@media screen and (min-width: 980px){
    .sd-hd__nav a:hover{
        text-decoration:underline #fff;
    }
};
@media screen and (max-width: 980px){
    .sd-hd {
        width: auto;
        background:rgba(0,0,0,.5);
    }
    .sd-hd section{
        position: absolute;
        bottom:0;
        width:100%;
        margin-top:0;
    }
    .sd-hd__nav{
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .sd-hd header{
        background:none;
    }
    .sd-hd__nav a{
        margin-bottom:0;
    }
    section.z-top{
        position: fixed;
        top:0;
        bottom: auto;
        width:100%;
        z-index: 1;
    }
};
</style>




